@import url('https://fonts.googleapis.com/css2?family=Italianno&display=swap');

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ECBEFF; /* Fundo rosa      */
  padding: 20px;
height: 146px;
width: auto;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: -0;
  box-sizing: border-box;
}


.menu-button {
  font-size: 50px;
  background: none;
  border: none;
  cursor: pointer;
}

.brand {
  text-align: center;
  flex: 1;
}

.brand-title {
  font-family: 'Arial', sans-serif; /* Fonte simples e legível */
  font-size: 22px; /* Tamanho semelhante ao da imagem */
  font-weight: bold; /* Negrito para destaque */
  color: black; /* Cor preta para o título */
  text-align: center; /* Alinhamento centralizado */
  margin: 0; /* Remove margens padrão */
}

.brand-subtitle {
  font-family: 'Italianno', serif; /* Fonte com aparência clássica */
  font-size: 16px; /* Tamanho menor para o subtítulo */
  font-style: italic; /* Texto em itálico */
  color: #000000; /* Cor cinza escuro para suavidade */
  text-align: center; /* Alinhamento centralizado */
  margin-top: 5px; /* Espaço controlado entre título e subtítulo */
}

.cart {
  font-size: 24px;
  cursor: pointer;
}

.sidebar {
  position: absolute;
  margin-top: 200px; /* Move o elemento 50px para baixo */
  left: 0;
  height: 100%;
  width: 250px;
  background-color: #ffffff;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
  
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 20px;
}

.sidebar ul li {
  margin: 10px 0;
}

.sidebar ul li a {
  text-decoration: none;
  color: #333;
  font-size: 18px;
}

.sidebar ul li a:hover {
  color: #ff79ed;
}

/* Faixa preta no rodapé */
.footer-bar {
  background-color: black; /* Cor de fundo preta */
  color: white; /* Texto branco */
  padding: 10px 0; /* Espaçamento interno */
  display: flex; /* Usando Flexbox */
  justify-content: center; /* Centraliza horizontalmente o conteúdo */
  align-items: center; /* Alinha itens verticalmente */
  flex-wrap: wrap; /* Permite quebra de linha para telas pequenas */
}

/* Container interno para centralizar o conteúdo */
.footer-content {
  display: flex;
  flex-direction: row; /* Alinhamento em linha */
  gap: 20px; /* Espaço entre itens */
  flex-wrap: wrap; /* Quebra de linha em telas menores */
  max-width: 1200px; /* Largura máxima do conteúdo */
  width: 100%; /* Ocupa toda a largura disponível */
  justify-content: space-evenly; /* Espaço uniforme entre os itens */
}

/* Estilo de cada item */
.footer-item {
  font-family: Arial, sans-serif; /* Fonte limpa */
  font-size: 14px; /* Tamanho do texto */
  display: flex; /* Alinhamento interno dos itens */
  align-items: center; /* Alinha ícones e texto verticalmente */
  gap: 5px; /* Espaço entre ícones e texto */
  white-space: nowrap; /* Impede quebra de linha dentro de um item */
}

/* Ícones */
.footer-item i {
  font-size: 16px; /* Tamanho dos ícones */
  color: white; /* Ícones brancos */
}

/* Tornar responsivo */
@media (max-width: 768px) {
  .footer-content {
      flex-direction: column; /* Empilha os itens em telas pequenas */
      align-items: center; /* Centraliza os itens */
  }

  .footer-item {
      text-align: center;
  }
}
